import {mapActions, mapState} from 'vuex'
import dummypreviews from '@/components/chats/dummypreviews/index.vue'
import preview from '@/components/chats/preview/index.vue'
import contact from '@/components/contact/index.vue'
import {SwipeList, SwipeOut} from 'vue-swipe-actions';
import userspic from '@/components/assets/userspic/index.vue'
import { EventTimelineSet } from 'matrix-js-sdk';

export default {
  name: "chatsList",
  data: function () {

    return {
      loading: false,
      textValue: '',
      enabled: true,
      page: 0,
      revealed: {},
      lastEventDescription: '',
    }

  },
  components: {
    preview,
    SwipeOut,
    SwipeList,
    userspic,
    dummypreviews,
    contact
  },

  props: {
    user_data: {
      type: String,
      default: () => {
      }
    }
  },
  created: () => {

  },

  watch: {
    //$route: 'getdata'
  },

  beforeRouteEnter(to, from, next) {
    next()
  },

  beforeRouteUpdate(to, from, next) {
    next()
  },
  beforeDestroy() {
    window.removeEventListener('keydown', this.onKeyDown);
    window.removeEventListener('keyup', this.onKeyUp);
  },

  computed: mapState({
    auth: state => state.auth,

    ...mapState([
      'minimized',
      'active',
      'pocketnet'
    ]),
    rooms: function (){
      return this.core.mtrx.client.getRooms()
    },

    empty : function(){
      return this.core.mtrx.ready && this.chats.length === 0
    },

    activeuser : function(){
      return this.core.user.userinfo
    },

    chats : function(state){
      console.log('state.chats', state.chats)
      return state.chats
    }

  }),
  methods: {
   
    dd(v) {
      console.log(v.target);
    },
    revealFirstRight() {
      this.$refs.list.revealRight(0);
    },
    revealFirstLeft() {
      this.$refs.list.revealLeft(0);
    },

    remove(item) {
      this.$set(this.mockSwipeList, this.page, this.mockSwipeList[this.page].filter(i => i !== item));
    },
    setLastEvent(name, {item, index}) {
      this.lastEventDescription = {
        name,
        index,
        id: item.id,
      };
    },
    itemClick(e) {
      console.log(e, 'item click');
    },
    fbClick(e) {
      console.log(e, 'First Button Click');
    },
    sbClick(e) {
      console.log(e, 'Second Button Click');
    },
    // keyboard
    onKeyDown(e) {
      if (e.keyCode !== 16) return;
      this.enabled = false;
    },
    onKeyUp(e) {
      if (e.keyCode !== 16) return;
      this.enabled = true;
    },
    removeRoom() {
      this.core.mtrx.client.leave(this.chat.roomId)
    }
  },
  mounted() {
    // ideally should be in some global handler/store
    window.addEventListener('keydown', this.onKeyDown);
    window.addEventListener('keyup', this.onKeyUp);

  }
}